.neon-text {
    font-size: 14rem;
    color: #ffd8d8;
  }
  .neon-wrapper {
    display: block;
    box-shadow:
    0 0 0.1vw  0.4vw #f8fff7,   
    0 0 0.4vw  0.6vw #7ce972, 
    0 0   4vw  0.4vw #3ee50b,
    inset 0 0 1.5vw  0.4vw #36e50b,
    inset 0 0 0.4vw  0.2vw #7ce972,
    inset 0 0 0.5vw  0.2vw #f8fff7;
    border-radius: 0rem;
    position: absolute;
  }
  .neon-text {
    padding: 0 3.5rem 0 3.5rem;
  }
