img{
  width: 100% ;
  height:100% ;
}

#a1{
  position: absolute;
  left: -50vw;
  top: 27.5vh;
  line-height: 80px;
  text-align: left;
  letter-spacing: 0.15em;
  margin-right: 10px;
  color: #EFEFEF;
  background-color: rgba(0, 0, 0, 0);
  width: 45%;
  height: auto;
}

#title1{
  font-family: 'Source Code Pro', monospace;
  font-weight: 400;
  color: rgb(192, 181, 181); /*rgb(51, 48, 48)*/
  font-size: 4vw;
  line-height: 5vw;
  color: #00fc0d;
  font-family: 'Roboto Mono', monospace;
}



#a2{
  display: flex;
  flex-direction: column;
  position: absolute;
  width: 50vw;
  height: 75vh;
  right: 2vw;
  bottom: -75vh;
  border-radius: 2% 2% 0 0;
  background: #FFFFFF;
  background-color: rgba(0, 0, 0, 0);
  width: 50vw;
}



.aboutContent{
  margin: 1rem 0rem;
  padding: 1rem 0rem;
  font-family: 'Source Code Pro', monospace;
  font-weight: 400;
  font-style: normal;
  font-size: 2vw; /*2.6vw*/
  color: white;
  width: 70vw;
  opacity: 0;
}


.blinking{
  font-size: 6vw;
  animation:blinkingText 1.2s infinite;
}
@keyframes blinkingText{
  0%{     color: #00fc0d;    }
  49%{    color: #00fc0d; }
  60%{    color: white; }
  99%{    color:white;  }
  100%{   color: #00fc0d;    }
}



#about-overlay{
  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;
  top: 0;
  height: 100vh;
  width: 100vw;
  z-index: 10;
  display: block;
  overflow-y: hidden;
  overflow-x: hidden;
  background-color: black;
